* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root {
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    --text-primary: #2b2b2b;
    --text-secondary: #b984ff;
    --theme-primary: #6197fc;
    --theme-secondary: #154091;
}

.App {
    
}

.MainContent {
    border: 1px solid black;
    border-radius: 12px;
    padding: 1rem;
    margin: 2rem auto;
    width: 75vw;
    text-align: center;
}

.NavContainer {
    display: flex;
    height: 4rem;
    background: var(--theme-primary);
    align-items: center;
}

.NavHeader {
    display: flex;
    align-items: center;
    height: 100%;
}

.NavTitle, .NavLogo {
    color: #fff;
    flex: 1;
    font-size: 1.5rem;
}

.NavTitle {
    
}

.NavLogo {
    margin: 0 1rem;
}

.LoginForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 50%;
    margin: 0 auto;
}

.LoginField {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 1rem 0;
}

.LoginField label {
    flex: 1;
    text-align: left;
    margin: auto;
}

.LoginField input {
    flex: 3;
    min-width: 0;
    padding: 0.3rem;
    border: 1px solid var(--theme-secondary);
    border-radius: 0.75rem;
    outline: none;
}

.LoginForm button {
    width: 100%;
    padding: 0.3rem;
    color: #fff;
    background: var(--theme-primary);
    border: 1px solid var(--theme-secondary);
    border-radius: 0.75rem;
}

@media screen and (max-width: 960px) {
    .NavContainer {
        justify-content: center;
    }

    .LoginForm {
        width: 75%;
    }

    .LoginField {
        flex-direction: column;
    }

    .LoginField label {
        margin: 0;
    }
  }